.close-btn {
	width: 40px;
	height: 40px;
	background: var(--general-1);
	border: none;
}

.dialog-header {
	height: 60px;
	border-bottom: 1px solid var(--neutral-2);
}

.dialog-actions {
	height: 70px;
	border-top: 1px solid var(--neutral-2);
}

.agent-name {
	border: 1px solid var(--error-3);
	border-radius: 4px;
	height: 40px;
	line-height: 40px;
}

.alert-icon {
	display: inline-block !important;
	width: 20px;
	height: 20px;
	font-size: 20px;
	vertical-align: sub;
}

.cancel-btn {
	border: 1px solid var(--general-4) !important;
	color: unset !important;
}
